'use client';

import React from 'react';
import useSWR from 'swr';
import {
    Users, DollarSign, Clock, AlertCircle,
    Gamepad2, Play, Pause, Square, Power, Plus
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

export default function DashboardPage() {
    const { data: tablesData, mutate } = useSWR('/api/tables', fetcher, { refreshInterval: 5000 }); // Poll every 5s
    const { data: shiftData } = useSWR('/api/reports/shift', fetcher);

    const tables = tablesData?.data || [];
    const activeTables = tables.filter((t: any) => t.status === 'ACTIVE').length;
    const totalTables = tables.length;
    const shiftSales = shiftData?.data?.totalSales || 0;

    const handleStart = async (tableId: string) => {
        // For MVP, just quick start with Guest
        await fetch(`/api/tables/${tableId}/start`, {
            method: 'POST',
            body: JSON.stringify({ customerName: 'Guest' })
        });
        mutate();
    };

    const handleStop = async (tableId: string) => {
        if (confirm('Stop Session?')) {
            await fetch(`/api/tables/${tableId}/stop`, { method: 'POST' });
            mutate();
        }
    };

    return (
        <div className="flex flex-col h-full bg-[#050a07] overflow-hidden">
            {/* Header */}
            <header className="px-8 py-6 border-b border-[#1e3328] bg-[#0f1a14] flex justify-between items-center">
                <div>
                    <h2 className="text-2xl font-bold text-white mb-1">Dashboard</h2>
                    <p className="text-slate-400 text-sm">Welcome back, Admin</p>
                </div>
                <div className="flex gap-4">
                    <div className="bg-[#16261d] border border-[#1e3328] px-4 py-2 rounded-lg flex items-center gap-3">
                        <div className="size-8 rounded bg-[#22c55e]/20 flex items-center justify-center">
                            <DollarSign className="text-[#22c55e]" size={18} />
                        </div>
                        <div>
                            <p className="text-[10px] text-slate-400 uppercase font-bold">Shift Sales</p>
                            <p className="text-sm font-bold text-white">Rp {shiftSales.toLocaleString()}</p>
                        </div>
                    </div>
                    <div className="bg-[#16261d] border border-[#1e3328] px-4 py-2 rounded-lg flex items-center gap-3">
                        <div className="size-8 rounded bg-blue-500/20 flex items-center justify-center">
                            <Gamepad2 className="text-blue-500" size={18} />
                        </div>
                        <div>
                            <p className="text-[10px] text-slate-400 uppercase font-bold">Active Tables</p>
                            <p className="text-sm font-bold text-white">{activeTables} / {totalTables}</p>
                        </div>
                    </div>
                </div>
            </header>

            {/* Content */}
            <main className="flex-1 overflow-y-auto p-8">
                <h3 className="text-lg font-bold text-white mb-4 flex items-center gap-2">
                    <Gamepad2 className="text-[#22c55e]" /> Live Status
                </h3>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                    {tables.map((table: any) => (
                        <div key={table.id} className={`
                        relative rounded-xl border p-6 transition-all duration-300
                        ${table.status === 'ACTIVE'
                                ? 'bg-[#0f1a14] border-[#22c55e] shadow-[0_0_20px_rgba(34,197,94,0.1)]'
                                : 'bg-[#0f1a14] border-[#1e3328] opacity-80 hover:opacity-100'
                            }
                    `}>
                            {/* Status Badge */}
                            <div className="absolute top-4 right-4 text-xs font-bold uppercase tracking-wider py-1 px-2 rounded border
                           ${table.status === 'ACTIVE' ? 'text-[#22c55e] bg-[#22c55e]/10 border-[#22c55e]/20' : 'text-slate-500 bg-slate-900 border-slate-800'}
                        ">
                                {table.status}
                            </div>

                            <div className="flex items-center gap-4 mb-6">
                                <div className={`size-16 rounded-full flex items-center justify-center text-xl font-bold border-4
                                ${table.status === 'ACTIVE' ? 'border-[#22c55e] text-white bg-[#22c55e]/20' : 'border-slate-700 text-slate-600 bg-slate-800'}
                            `}>
                                    {table.id}
                                </div>
                                <div>
                                    <h4 className="font-bold text-white">{table.name}</h4>
                                    <p className="text-sm text-slate-500">{table.type} Area</p>
                                </div>
                            </div>

                            {table.status === 'ACTIVE' && table.activeSession ? (
                                <div className="space-y-4">
                                    <div className="bg-[#16261d] rounded p-3 border border-[#1e3328]">
                                        <div className="flex justify-between text-sm mb-1">
                                            <span className="text-slate-400">Duration</span>
                                            <span className="text-white font-mono">{table.activeSession.durationMinutes} min</span>
                                        </div>
                                        <div className="flex justify-between text-sm">
                                            <span className="text-slate-400">Current Bill</span>
                                            <span className="text-[#22c55e] font-bold">Rp {table.activeSession.currentBill.toLocaleString()}</span>
                                        </div>
                                    </div>
                                    <div className="flex items-center gap-2 text-xs text-slate-500">
                                        <Users size={14} /> {table.activeSession.customerName}
                                    </div>
                                    <button
                                        onClick={() => handleStop(table.id)}
                                        className="w-full py-2 bg-red-500/10 hover:bg-red-500/20 text-red-500 border border-red-500/20 rounded font-bold transition-colors flex items-center justify-center gap-2"
                                    >
                                        <Square size={16} fill="currentColor" /> STOP SESSION
                                    </button>
                                </div>
                            ) : (
                                <div className="space-y-4 pt-2">
                                    <p className="text-sm text-slate-500 text-center py-5"> Ready for new game </p>
                                    <button
                                        onClick={() => handleStart(table.id)}
                                        className="w-full py-2 bg-[#22c55e] hover:bg-[#16a34a] text-black font-bold rounded transition-colors flex items-center justify-center gap-2"
                                    >
                                        <Play size={16} fill="currentColor" /> START
                                    </button>
                                </div>
                            )}
                        </div>
                    ))}
                </div>
            </main>
        </div>
    );
}
